import {
    TOOL_FETCH,
    VIDEOS_FETCH,
    REGISTER_FETCH,
    REGISTER_DELETE,
    REGISTER_SAVE,
    GRAPH_CHART,
    GRAPH_PIE,
    TOOL_FETCH_DESIGN,
    REGISTER_FETCH_TMP,
    SESSION_SET,
    SESSION_GET_ALL,
    PROFILE_INFO,
    STATS_KAPPA,
    STATS_MATRIX,
    STATS_KAPPA_PRO, TOOL_SET, RENJIN_EXECUTE,
    RSTUDIO_EXECUTE, RSTUDIO_ERROR, RSTUDIO_OK, PROFILE_SET, PROJECT_INFO_GET
} from '../lince/types';

const initialState = {
    projectInfo: {},
    videos: [],
    register: [],
    registerTemp: [],
    tool: [],
    toolDesign: [],
    chart: {},
    pie: {},
    sessionParams: {},
    profile: {},
    kappaIndex: [],
    kappaProIndex: [],
    contingencyMatrix: [],
    labResult: [],
    isFetching: false,
};

export default function (state = initialState, action) {
    switch (action.type) {
        case PROJECT_INFO_GET:
            return {
                ...state,
                projectInfo: action.payload
            };
        case VIDEOS_FETCH:
            return {
                ...state,
                videos: action.payload
            };
        case TOOL_FETCH:
            return {
                ...state,
                tool: action.payload
            };
        case TOOL_FETCH_DESIGN:
            return {
                ...state,
                toolDesign: action.payload
            };
        case TOOL_SET:
            return {
                ...state,
                tool: action.payload
            };
        case REGISTER_FETCH:
            return {
                ...state,
                register: action.payload
            };
        case REGISTER_FETCH_TMP:
            return {
                ...state,
                registerTemp: action.payload
            };
        case REGISTER_SAVE:
            return {
                ...state,
                register: action.payload
            };
        case REGISTER_DELETE:
            return {
                ...state,
                register: action.payload
            };
        case GRAPH_CHART:
            return {
                ...state,
                chart: action.payload
            };
        case GRAPH_PIE:
            return {
                ...state,
                pie: action.payload
            };
        case SESSION_SET:
            return {
                ...state,
                sessionParams: action.payload
            };
        case SESSION_GET_ALL:
            return {
                ...state,
                sessionParams: action.payload
            };
        case PROFILE_INFO:
            return {
                ...state,
                profile: action.payload
            };
        case PROFILE_SET:
            return {
                ...state,
                profile: action.payload
            };
        case STATS_KAPPA:
            return {
                ...state,
                kappaIndex: action.payload
            };
        case STATS_KAPPA_PRO:
            return {
                ...state,
                kappaProIndex: action.payload
            };
        case STATS_MATRIX:
            return {
                ...state,
                contingencyMatrix: action.payload
            };
        case RENJIN_EXECUTE:
            return {
                ...state,
                labResult: action.payload
            };
        //saga
        case RSTUDIO_EXECUTE:
            return {
                ...state,
                isFetching: true
            };
        case RSTUDIO_OK:
            return {
                ...state,
                isFetching: false,
                labResult: action.payload
            };
        case RSTUDIO_ERROR:
            return {
                ...state,
                isFetching: false,
                errorMessage: action.payload
            };
        default:
            return state;
    }
}
